
*{
  margin: 0px;
  padding: 0px;
 
}.main-con{
  width: 900px;
  height: auto;
  background-color: black;
  padding: 0px;
  margin-left: 300px;
  margin-top: 50px;
}

.container {
  width: 600px;
  height: 60px;
  background-color: darkslategrey;
  margin-top: 50px;
  padding: 50px;
  margin-left: 100px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 10px;
}

h1{
  color: white;
  text-align: center;
  margin-top: 50px;
}
.label{
  color: white;
}

.btn{
  background-color: darkorange;
  color: white;
  padding: 10px 20px;
  border-radius: 20px;
  border: none;
}
.input{
  border-radius: 10px;
  background-color: white;
  border: none;
  padding: 10px;
  color: black;
  
}
.list {
  margin-top: 10px;
  width: 700px;
  height: auto;
  list-style: none;
  padding: 0;
  background-color: darkslategray;
  color: white;
  margin-bottom: 50px;
  
  
}
.con2{
  background-color: black;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  
}

.li-list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  color: darkorange;
  font-size: 25px;
  
}
.btn2{
  background-color: white;
  color:red;
  padding: 10px 25px;
  border-radius: 20px;
  border: none;
}
.btn3{
  background-color: white;
  color:green;
  padding: 10px 25px;
  border-radius: 20px;
  border: none;
  margin-left: 300px;
}